<script>
import axios from 'axios'
/* eslint-disable */
export default {
 name: 'CotizacionView',
  components: {
    // HelloWorld
  },data(){
    return{
          enviado:false,
          preloader:false,
          form:{
            nombre:"",
            apellido:"",
            email:"",
            celular:"",
            servicio:"",
            mensaje:""
          },
          errors:[],
    }
  },
  beforeMount () {

  },
  mounted(){
    this.goTo("cotizar")
  },
methods: {
   // the function to call when the user scrolls, added as a method
         enviarPresupuesto(){
          if (this.checkForm()) {
            // var config={
            //         headers: {"Access-Control-Allow-Origin": "*"}
            //       }
             var form=this.form
             this.preloader=true
             var url="./forms/enviarPresupuesto.php";
              axios
                .post(url,{form})
                .then(response => {
                  if(response.data === 'ok'){
                    this.enviado=true;
                    // this.e1=4
                  }else{
                    alert("error");
                  }
              })
           }

          },
    checkForm: function () {
      this.errors = [];
      if (!this.form.nombre) {
        this.errors.push("El nombre es obligatorio.");
      }
      if (!this.form.apellido) {
        this.errors.push("El apellido es obligatorio.");
      }
      if (!this.form.email) {
        this.errors.push('El correo electrónico es obligatorio.');
      } else if (!this.validEmail(this.form.email)) {
        this.errors.push('El correo electrónico debe ser válido.');
      }
      if (!this.form.celular) {
        this.errors.push("El celular es obligatorio.");
      }
      if (!this.form.servicio) {
        this.errors.push("Seleccione una opcion.");
      }
      if (!this.form.mensaje) {
        this.errors.push("El mensaje es obligatorio.");
      }
      if (!this.errors.length) {
        return true;
      }

    },
    validEmail: function (email) {
      var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(email);
    },
    goTo(refName) {
          // console.log(refName)
            var element = this.$refs[refName];
            var top = element.offsetTop;
            window.scrollTo({ top: top, behavior: 'smooth' })

            //  window.scrollTo(0, top);
          }
},created(){
  if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
     this.isMobile=true
   }else{
    this.isMobile=false
    }


}
}
</script>

